.auth-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.auth-modal {
  background: #ffffff;
  padding: 2rem;
  border-radius: 12px;
  width: 360px;
  box-shadow: 0 0 20px rgba(0,0,0,0.3);
  position: relative;
}

.close-btn {
  position: absolute;
  top: 12px;
  right: 16px;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

.auth-tabs {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid #ddd;
}

.auth-tabs button {
  background: none;
  border: none;
  font-size: 1.1rem;
  padding: 0.5rem;
  cursor: pointer;
}

.auth-tabs .active {
  border-bottom: 3px solid #000;
  font-weight: bold;
}

.auth-content input {
  display: block;
  width: 100%;
  padding: 0.6rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.auth-content button {
  width: 100%;
  background: #000;
  color: #fff;
  padding: 0.6rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}
